import * as React from 'react';
import classNames from 'classnames';
import { antDesignIcons } from '@ant-design/icons';
import ReactIcon from '@ant-design/icons-react';
import createFromIconfontCN from './IconFont';
import { svgBaseProps } from './utils';
import warning from '../_util/warning';
ReactIcon.add(...antDesignIcons);
const Icon = props => {
    const { 
    // affect outter <i>...</i>
    title, className, onClick, style, 
    // affect inner <svg>...</svg>
    type, component: Component, viewBox, spin, svgClassName, svgStyle = {}, 
    // children
    children, } = props;
    warning(Boolean(type || Component || children), 'Icon should have `type` prop or `component` prop or `children`.');
    const classString = classNames({
        [`m-icon`]: true,
        [`m-icon-${type}`]: Boolean(type),
    }, className);
    const svgClassString = classNames({
        [`m-icon-spin`]: !!spin || type === 'loading',
    }, svgClassName);
    // component > children > type
    if (Component) {
        const innerSvgProps = Object.assign({}, svgBaseProps, { className: svgClassString, style: svgStyle, viewBox });
        if (!viewBox) {
            delete innerSvgProps.viewBox;
        }
        return (<i className={classString} title={title} style={style} onClick={onClick}>
        <Component {...innerSvgProps}>{children}</Component>
      </i>);
    }
    if (children) {
        warning(Boolean(viewBox), 'Make sure that you provide correct `viewBox`' + ' prop (default `0 0 1024 1024`) to Icon.');
        const innerSvgProps = Object.assign({}, svgBaseProps, { className: svgClassString, style: svgStyle });
        return (<i className={classString} title={title} style={style} onClick={onClick}>
        <svg {...innerSvgProps} viewBox={viewBox}>
          {children}
        </svg>
      </i>);
    }
    if (type) {
        return (<i className={classString} title={title} style={style} onClick={onClick}>
        <ReactIcon className={svgClassString} type={type} style={svgStyle}/>
      </i>);
    }
    return <i className={classString} title={title} style={style} onClick={onClick}/>;
};
Icon.displayName = 'Icon';
Icon.createFromIconfontCN = createFromIconfontCN;
export default Icon;



